<template>
    <router-view></router-view>
</template>
<script>
export default {
    name: 'auth',
    data() {
        return {
            userInfo: this.$store.state.userInfo
        };
    },
    created() {
        if (this.userInfo.userRole === 'brushhand') {
            this.$router.replace({
                path: '/auth/seller'
            });
        }
        // 商家跳转到商家页面
        if (this.userInfo.userRole === 'business') {
            this.$router.replace({
                path: '/auth/buyer'
            });
        }
    }
};

</script>



